class filtre {
  constructor(dateMonth, filters = []) {
    /**
     * Date de l'application sans les les heures.
     */
    this.dateMonth = dateMonth;
    this.filters = filters;
  }
  /**
   * permet de valider un date, active ou pas.
   * @param {*} date Doit etre un object moment.
   */
  ValidationDay(date) {
    return new Promise(resolvEnd => {
      if (date.diff(this.dateMonth, "day") >= 0) {
        resolvEnd(this.statusDate(date, true, "default-disable-app-date"));
      } else {
        resolvEnd(this.statusDate(date, false, "default-disable-app-date"));
      }
    });
  }
  statusDate(date, status, custom_class, select = "") {
    return {
      status: status,
      custom_class: custom_class,
      date: date.date(),
      select: select,
      date_french: date.format("YYYY-MM-DD")
    };
  }
}
export default filtre;
